import React, { Component } from "react";
import InfoDisp from "../Stateless/infodisp";
import { connect } from "react-redux";
import "../../NavBar/NavBar.scss";
import { injectIntl, FormattedMessage } from "react-intl";

const USRADD = "3000 Steeles AveE,309,Markham,Ontario";
const USRCT = "1217chan@gamil.com";

let CONTACT_ADD = "";
let CONTACT_US = "";

let ADDMSG = [CONTACT_ADD, USRADD];

let CTACMSG = [CONTACT_US, USRCT];

class Address extends Component {
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  render() {
    ADDMSG[0] = this.handleStr("CONTACT_ADD");
    CTACMSG[0] = this.handleStr("CONTACT_US");

    return (
      <div className="NavADD">
        <InfoDisp name="navAddress" logo="fas fa-home" message={ADDMSG} />
        <InfoDisp name="navContact" logo="fas fa-envelope" message={CTACMSG} />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return state;
}

export default connect(mapStateToProps)(injectIntl(Address));

