import * as types from "../const/types";
import option from "../const/fetchoption"

export const fetchDetailBegin = () => ({
  type: types.FETCH_DETAIL_BEGIN
});

export const fetchDetailSuccess = houses => ({
  type: types.FETCH_DETAIL_SUCCESS,
  payload: { houses }
});

export const fetchDetailFailure = error => ({
  type: types.FETCH_DETAIL_FAILURE,
  payload: { error }
});

export default function fetchDetail(query) {
  return dispatch => {
    dispatch(fetchDetailBegin());
    return fetch(query,option)
      .then(res => {
        //console.log(res)
        return res.json()
      })
      .then(json => {
        //console.log(json)
        dispatch(fetchDetailSuccess(json));
        return json;
      })
      .catch((error) => {
        //console.log(error)
        dispatch(fetchDetailFailure(error))});
  };
}
