import React, { Component } from "react";
import Slider from "react-slick";
import { connect } from "react-redux";
import { injectIntl, FormattedMessage } from "react-intl";
import LazyLoad from "react-lazy-load";
import "antd/dist/antd.css";
import {
  Button,
  ButtonToolbar,
  Row,
  Col,
  Image,
  Container
} from "react-bootstrap";
import {
  Route,
  Link,
  BrowserRouter as Router,
  withRouter
} from "react-router-dom";
import imgHolder from "../../../img/imagesHolder.png";

import { Card, Tag } from "antd";

class ListingShow extends Component {
  //ret a string of locale message
  constructor() {
    super();
    this.state = {
      filter: "DEFAULT"
    };
  }
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  handleClick = val => {
    //console.log(val.target.getAttribute("data"))
    this.setState({
      filter: val.target.getAttribute("data")
    });
  };

  handleTags = ele => {
    let info = ele.info === undefined ? {} : ele.info;
    let dom = [];
    for (let i in info) {
      if (
        i !== "description" &&
        (i === "bed" || i === "barth" || i === "park")
      ) {
        dom.push(<Tag>{i + " " + info[i]}</Tag>);
      }
    }
    return dom;
  };

  sort = filter => {
    let tmp =
      this.props.search.houses === undefined ? [] : this.props.search.houses;
    if (tmp.status === 500) tmp = [];

    let a = [...tmp];
    console.log(filter);
    if (filter === "LATEST") {
      a.sort(function(a, b) {
        var keyA = new Date(a.updatedAt),
          keyB = new Date(b.updatedAt);
        // Compare the 2 dates
        if (keyA < keyB) return -1;
        if (keyA > keyB) return 1;
        return 0;
      });
    } else if (filter === "LEAST_PRICE") {
      a.sort(function(a, b) {
        var keyA = a.price,
          keyB = b.price;
        if (keyA < keyB) return -1;
        if (keyA > keyB) return 1;
        return 0;
      });
    }

    return a;
  };

  render() {
    return (
      <div>
        <div className="list_filters">
          <ButtonToolbar>
            <Button
              variant="light"
              data="DEFAULT"
              onClick={e => this.handleClick(e)}
            >
              {this.handleStr("DEFAULT_FILTER")}
            </Button>
            <Button
              variant="light"
              data="LATEST"
              onClick={e => this.handleClick(e)}
            >
              {this.handleStr("LATEST_FILTER")}
            </Button>
            <Button
              variant="light"
              data="LEAST_PRICE"
              onClick={e => this.handleClick(e)}
            >
              {this.handleStr("LEAST_PRICE_FILTER")}
            </Button>
          </ButtonToolbar>
          <Container>
            <Row>
              {this.sort(this.state.filter).map((ele, idx) => (
                <LazyLoad height={500} offsetVertical={100} >
                  <Link
                    to={{ pathname: `/detail/${ele.id}` }}
                    style={{ color: "white" }}
                  >
                    <Card
                      style={{ width: "16em",marginRight:"2em", marginTop:"1em"}}
                      
                      hoverable
                      cover={
                        <Image
                          variant="top"
                          src={
                            ele.images[0] === undefined
                              ? ""
                              : ele.images[0].location
                          }
                          onError={e => {
                            e.target.onError = null;
                            e.target.src = imgHolder;
                          }}
                        />
                      }
                    >
                      {console.log(ele)}
                      <Card.Meta
                        title={ele.loc.addr}
                        description={
                          <div>
                            <h4 style={{ color: "#e77641" }}>
                              {"$" + ele.price}
                            </h4>
                            <hr />
                            <p>{this.handleTags(ele)}</p>
                          </div>
                        }
                      />
                    </Card>
                  </Link>
                </LazyLoad>
              ))}
            </Row>
          </Container>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return state;
}

export default connect(mapStateToProps)(injectIntl(ListingShow));
