import {
    FETCH_SALEOUT_BEGIN ,
    FETCH_SALEOUT_SUCCESS,
    FETCH_SALEOUT_FAILURE 

  } from "../const/types";
  
  
  import actions from "../actions";
  
  const initialState = {
    houses: [],
    loading: false,
    error: null
  };
  
  export default function saleoutReducer(state = initialState, action) {
    switch (action.type) {
      case FETCH_SALEOUT_BEGIN:
        return {
          ...state,
          loading: true,
          error: null
        };
      case FETCH_SALEOUT_SUCCESS:
        return {
          ...state,
          loading: false,
          houses: action.payload.houses
        };
      case FETCH_SALEOUT_FAILURE:
        return {
          ...state,
          loading: false,
          error: action.payload.error,
          houses: action.houses
        };
      default:
        return state;
    }
  }
  