import React, { Component } from "react";
import Slider from "react-slick";
import { connect } from "react-redux";
import { injectIntl, FormattedMessage } from "react-intl";
import { Form, FormControl, Button, Card, Row, Col } from "react-bootstrap";

import './subscribe.scss';

class Subscribe extends Component {
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  render() {
    return (
      <div>
      <Card style={{ width: "16rem",marginTop:"2rem" }}>
      <Row>
        <Col style={{ marginTop:"0.8rem" }}>
          <h5>订阅消息</h5>
        </Col>
      </Row>
      <Row>
        <Col style={{ marginTop:"0.3rem",marginBottom:"1rem"  }}>
        <Form id="subscribe-form" className="col" action="#" method="post">
          <FormControl type="text" placeholder="email" className="mr-sm-2" />
          <Button type="submit" style={{ fontSize:"0.8em" }}>{this.handleStr("SEND")}</Button>
        </Form>
        </Col>
        </Row>
        </Card>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return state;
}

export default connect(mapStateToProps)(injectIntl(Subscribe));
