import React, { Component } from "react";
import { connect } from "react-redux";
import "./NavBar.scss";
import { injectIntl, FormattedMessage } from "react-intl";
import { Container, Row, Col, Button } from "react-bootstrap";
import fetchHouses from "../../actions/houseAction";
import * as query from "../../const/api";
import areaCode from "../../const/areacode";
import listType from "../../const/listType";
import {
  Route,
  Link,
  BrowserRouter as Router,
  withRouter
} from "react-router-dom";
class SearchPOP extends Component {
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  constructor() {
    super();
    this.state = {
      city: "Unlimited",
      listType: "Unlimited",
      min: 1,
      max: 40000000,
      parkSpace: "1-2",
      bedRoom: "1",
      room: "1"
    };
  }

  //todo finish the query part
  handleClick = val => {
    // use fallback to wait for state mutate 
    let search = this.handleQuery(query.BASE_QUERY);
    console.log(search)
    this.props.dispatch(fetchHouses(search));
  };

  handleQuery = query => {
    query += "property/search?";
    if (this.state.city !== "Unlimited") {
      query += `rid=${this.state.city}&`;
    }
    if (this.state.listType !== "Unlimited") {
      query += `forType=${this.state.listType}&`;
    }
    query += `minprice=${this.state.min}&`;
    query += `maxprice=${this.state.max}&`;
    let rooms = this.handleNum(this.state.room)
    query += `maxroom=${rooms.max}&`
    query += `minroom=${rooms.min}&`

    query += "state=NORMAL"
    return query

  };

  handleNum = num => {
    let res = {};
    switch (num) {
      case "1":
        res = { min: 1, max: 1 };
        break;
      case "1-2":
        res = { min: 1, max: 2 };
        break;
      case "2":
        res = { min: 2, max: 2 };
        break;
      case "3":
        res = { min: 3, max: 3 };
        break;
      case "3-4":
        res = { min: 3, max: 4 };
        break;
      case "4+":
        res = { min: 4, max: 1000 };
        break;
      case "5+":
        res = { min: 5, max: 1000 };
        break;
      default:
        res = { min: 1, max: 1000 };
    }
    return res
  };

  handleCity = val => {
    console.log(val.target.value);
    this.setState({
      city: val.target.value
    });
  };

  handleListType = val => {
    console.log(val.target.value);
    this.setState({
      listType: val.target.value
    });
  };

  handleMin = val => {
    console.log(val.target.value);
    this.setState({
      min: val.target.value
    });
  };

  handleMax = val => {
    console.log(val.target.value);
    this.setState({
      max: val.target.value
    });
  };

  handleParkingSpace = val => {
    console.log(val.target.value);
    this.setState({
      parkSpace: val.target.value
    });
  };

  handleBedRoom = val => {
    console.log(val.target.value);
    this.setState({
      bedRoom: val.target.value
    });
  };

  handleRoom = val => {
    console.log(val.target.value);
    this.setState({
      room: val.target.value
    });
  };

  render() {
    //console.log(this.state);
    return (
      <div className="search">
        <Container>
          <div id="cd-search" className="cd-search">
            <form action="#" method="post">
              <div className="cd-search-content">
                <form>
                  <Row className="row">
                    <Col className="form-group col-md-6">
                      <label for="inputEmail4">{this.handleStr("CITY")}</label>
                      <select
                        id="inputCity"
                        className="form-control"
                        onChange={e => this.handleCity(e)}
                      >
                        <option selected>{this.handleStr("UNLIMITED")}</option>
                        {areaCode.map((ele, idx) => (
                          <option value={ele.id}>
                            {this.handleStr(
                              ele.val.replace(/\s+/g, "").toUpperCase()
                            )}
                          </option>
                        ))}
                      </select>
                    </Col>
                    <Col>
                      <div className="form-group col-md-6">
                        <label for="inputState">
                          {this.handleStr("LISTING_TYPE")}
                        </label>
                        <select
                          id="inputState"
                          className="form-control"
                          onChange={e => this.handleListType(e)}
                        >
                          {listType.map((ele, idx) => (
                            <option>
                              {this.handleStr(
                                ele.val.replace(/\s+/g, "").toUpperCase()
                              )}
                            </option>
                          ))}
                        </select>
                      </div>
                    </Col>
                  </Row>
                  <Row className="row">
                    <Col className="form-group col-md-5">
                      <label for="inputAddress">{this.handleStr("FROM")}</label>
                      <input
                        type="text"
                        className="form-control"
                        id="inputMin"
                        placeholder="$1"
                        onChange={e => this.handleMin(e)}
                      />
                    </Col>
                    <Col className="form-group col-md-5">
                      <label for="inputAddress2">{this.handleStr("TO")}</label>
                      <input
                        type="text"
                        className="form-control"
                        id="inputAddress2"
                        placeholder="$4000000"
                        onChange={e => this.handleMax(e)}
                      />
                    </Col>
                  </Row>
                  <Row className="row">
                    <Col className="form-group col-md-4">
                      <label for="inputState">
                        {this.handleStr("PARKING_SPACE")}
                      </label>
                      <select
                        id="inputState"
                        className="form-control"
                        onChange={e => this.handleParkingSpace(e)}
                      >
                        <option selected>1-2</option>
                        <option>3-4</option>
                        <option>5+</option>
                      </select>
                    </Col>
                    <Col className="form-group col-md-4">
                      <label for="inputState">
                        {this.handleStr("BEDROOM")}
                      </label>
                      <select
                        id="inputState"
                        className="form-control"
                        onChange={e => this.handleBedRoom(e)}
                      >
                        <option selected>1</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4+</option>
                      </select>
                    </Col>
                    <Col className="form-group col-md-4">
                      <label for="inputState">{this.handleStr("ROOM")}</label>
                      <select
                        id="inputState"
                        className="form-control"
                        onChange={e => this.handleRoom(e)}
                      >
                        <option selected>1</option>
                        <option>2</option>
                        <option>3</option>
                        <option>4+</option>
                      </select>
                    </Col>
                  </Row>

                  <Button variant="primary" onClick={e => this.handleClick(e)}>
                    <Link to="/listing" style={{color:"white"}}> {this.handleStr("SEARCH")}</Link>
                  </Button>
                </form>
              </div>
            </form>
          </div>
        </Container>
      </div>
    );
  }
}

function mapStateToProps(state) {
  //console.log(state.search);
  const props = {
    houses: state.search.houses,
    loading: state.search.loading,
    error: state.search.error
  };

  return props;
}

export default connect(mapStateToProps)(injectIntl(SearchPOP));
