import * as types from "../const/types";
import option from "../const/fetchoption"

export const fetchHouseBegin = () => ({
  type: types.FETCH_HOUSE_BEGIN
});

export const fetchHouseSuccess = houses => ({
  type: types.FETCH_HOUSE_SUCCESS,
  payload: { houses }
});

export const fetchHouseFailure = error => ({
  type: types.FETCH_HOUSE_FAILURE,
  payload: { error }
});

export default function fetchHouses(query) {
  return dispatch => {
    dispatch(fetchHouseBegin());
    return fetch(query,option)
      .then(res => {
        //console.log(res)
        return res.json()
      })
      .then(json => {
        //console.log(json)
        dispatch(fetchHouseSuccess(json));
        return json;
      })
      .catch((error) => {
        //console.log(error)
        dispatch(fetchHouseFailure(error))});
  };
}
