import React, { Component } from "react";
import Slider from "react-slick";
import { connect } from "react-redux";
import { injectIntl, FormattedMessage } from "react-intl";
import { Card, Button, Col, Image, Container, Row } from "react-bootstrap";
import settings from "../const/scrollsetting";
import innerBanner from "../img/imgback.jpg";
import Accordion from "../components/Common/States/accordtion";
import Broker from "../components/Common/States/brokersidbar";
import Subscribe from "../components/Common/States/subscribe";
import SideFrame from "../components/Common/States/sideframe";
import service from "../const/detailService";
import Services from "../components/Common/States/services";
import faq from "../const/faq";
import * as query from "../const/api"
import DetailInfo from "../components/Pages/detailpage/detailInfoFrame"

import {
  Route,
  Redirect,
  Link,
  BrowserRouter as Router,
  withRouter
} from "react-router-dom";

import update from "immutability-helper";
import fetchDetail from "../actions/detailAction";

import './detailpage.scss';

class DetailPage extends Component {
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  constructor(props) {
    super(props);
 
  }
  //todo correct id form router
  componentDidMount() {
    
      console.log(this.props.match.params.hid)
      this.props.dispatch(fetchDetail(query.BASE_QUERY+"property/"+this.props.match.params.hid+".json"));
      
  }
  
  redirect = () =>{
    if(this.props.error){
      return <Redirect to='/*' />
   }
  }

  render() {
    let settingsCopy = update(settings, { dots: { $set: false } });
    //console.log(this.state.id);
    
    return (
      <div id="main-detail">
        {
          this.redirect()
        }
        <div className="L_Frame1">
          <div class="L_Inner_Banner" style={{maxHeight:"200px",overflow:"hidden"}}>
            <Image src={innerBanner} fluid />
          </div>
        </div>
        <div className="L_Frame2">
          <Services services={service} />
        </div>
        <h1>{this.props.title}</h1>
        <br />
        <Container>
          <div className="L_Frame3">
            <Row>
              <Col className="L_Side" md="9">
                <SideFrame title="L_Side_Frame">
                  <DetailInfo info={this.props.house}/>
                </SideFrame>
              </Col>
              <Col className="R_Side" md={3}>
                <SideFrame title="R_Side_Frame">
                  <Broker />
                  <Accordion title="FAQs" accordions={faq} />
                  <Subscribe />
                </SideFrame>
              </Col>
            </Row>
          </div>
        </Container>
      </div>
    );
  }
}

function mapStateToProps(state) {
  //console.log(state.newListing);
  const props = {
    house: state.detail.house,
    loading: state.detail.loading,
    error: state.detail.error
  };

  return props;
}

export default connect(mapStateToProps)(injectIntl(DetailPage));
