import * as types from "../const/types";
import option from "../const/fetchoption"

export const fetchSaleOutBegin = () => ({
  type: types.FETCH_SALEOUT_BEGIN
});

export const fetchSaleOutSuccess = houses => ({
  type: types.FETCH_SALEOUT_SUCCESS,
  payload: { houses }
});

export const fetchSaleOutFailure = error => ({
  type: types.FETCH_SALEOUT_FAILURE,
  payload: { error }
});

export default function fetchSaleOut(query) {
  return dispatch => {
    dispatch(fetchSaleOutBegin());
    return fetch(query,option)
      .then(res => {
        //console.log(res)
        return res.json()
      })
      .then(json => {
        //console.log(json)
        dispatch(fetchSaleOutSuccess(json));
        return json;
      })
      .catch((error) => {
        //console.log(error)
        dispatch(fetchSaleOutFailure(error))});
  };
}
