import React, { Component } from 'react';


const InfoDisp = props =>(
 
  
   <div className={props.name}>
     <i id={props.name+"Logo"}className={props.logo}></i>
     <div id={props.name+"Content"}>
     {
            props.message.map((ele,idx) => (
                <span id={props.name+idx} key={props.name+idx}>{ele}</span>
            ))
     }
     </div>
   
   </div> 
   
)

export default InfoDisp