import {
    FETCH_BROKAGE_BEGIN,
    FETCH_BROKAGE_SUCCESS,
    FETCH_BROKAGE_FAILURE 

  } from "../const/types";
  
  
  import actions from "../actions";
  
  const initialState = {
    houses: [],
    loading: false,
    error: null
  };
  
  export default function brokageReducer(state = initialState, action) {
    switch (action.type) {
      case FETCH_BROKAGE_BEGIN:
        return {
          ...state,
          loading: true,
          error: null
        };
      case FETCH_BROKAGE_SUCCESS:
        return {
          ...state,
          loading: false,
          houses: action.payload.houses
        };
      case FETCH_BROKAGE_FAILURE:
        return {
          ...state,
          loading: false,
          error: action.payload.error,
          houses: action.houses
        };
      default:
        return state;
    }
  }
  