import React, { Component } from "react";
import Slider from "react-slick";
import { connect } from "react-redux";
import { injectIntl, FormattedMessage } from "react-intl";
import * as query from "../../../const/api";
import fetchHouses from "../../../actions/houseAction"
import {
  Route,
  Link,
  BrowserRouter as Router,
  withRouter
} from "react-router-dom";
import {
  Card,
  Button,
  ButtonToolbar,
  Col,
  Image,
  Container,
  Row
} from "react-bootstrap";

class InfoTag extends Component {
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  handleMore = () =>{
    this.props.dispatch(fetchHouses(query.FETCH_LATEST_LISTING));
  }
  render() {
    const zoning = this.props.zoning;
    const price = this.props.price;
    const addr = this.props.addr;
    const type = this.props.type;
    const id = this.props.id;

    return (
      <div>
        <Card className="InfoTag" style={{ width: "15em", height: "15em",padding:"0.5em" }}>
          <h4>{zoning}</h4>
          <h3>${price}</h3>
          <h4>{type}</h4>
          <h6>{this.handleStr("ADDRESS")}</h6>
          <h6>{addr}</h6>
          <Button variant="primary" size="sm">
            <Link to={{pathname:`/detail/${id}`}} style={{ color: "white" }}>
              {this.handleStr("DETAIL_INFO")}{" "}
              <i class="fa fa-angle-down" aria-hidden="true"></i>
            </Link>
          </Button>
          <Link to="/listing" onClick={()=>this.handleMore()}>
            more
          </Link>
        </Card>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return state;
}

export default connect(mapStateToProps)(injectIntl(InfoTag));
