import React, { Component } from "react";
import { connect } from "react-redux";
import { injectIntl, FormattedMessage } from "react-intl";
import { Nav, NavItem, Col, Row, Container, Image } from "react-bootstrap";
import bed from "../../../img/bed.png";
import parking from "../../../img/parking.png";
import chair from "../../../img/chair.png";
import settings from "../../../const/scrollsetting";
import fetchSaleOut from "../../../actions/saleoutAction";
import imgHolder from "../../../img/imagesHolder.png";
import { Button } from "react-bootstrap";
import * as query from "../../../const/api";
import update from "immutability-helper";
import { Card, Carousel,Statistic } from "antd";
const { Meta } = Card;
const {Countdown} = Statistic 


class HouseFrame extends Component {
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  componentDidMount() {
    this.props.dispatch(fetchSaleOut(query.FETCH_LATEST_BROKAGE));
  }

  handleError = e => {
    console.log(e);
    e.target.src = imgHolder;
  };

  render() {
    let settingsCopy = update(settings, {
      dots: { $set: false },
      slidesToShow: { $set: 4 },
      slidesToScroll: { $set: 4 },
      arrows: { $set: true },
      responsive: { $set: [] }
    });
    console.log(settingsCopy);
    let val = this.props.houses === undefined ? [] : this.props.houses;
    return (
      <div>
        <div id="recent-sale" className="recentSale" style= {{paddingTop:"2em"}}>
          <h3 style={{color:"white"}}>{this.handleStr("RECENT_SALE")}</h3>
          <Container>
            <Row  style= {{marginTop:"6em"}}>
              <Col style = {{ marginLeft:"12em"}}>
                <i class="far fa-building" style={{fontSize:"2.5em"}}></i>
                <p style={{fontSize:"2em"}}>{this.props.areas}</p>
                <p style={{fontSize:"1.5em"}}>{this.handleStr("AREA")}</p>
              </Col>
              <Col style = {{ marginRight:"12em"}}>
                {/* {todo: handle length void } */}
                <i class="fas fa-users" style={{fontSize:"2.5em"}}></i>
                <p  style={{fontSize:"2em"}}>{val.length}</p>
                <p  style={{fontSize:"1.5em"}}>{this.handleStr("CLIENTS")}</p>
              </Col>
            </Row>

            <Carousel {...settingsCopy} style= {{paddingBottom:"4em",marginTop:"3em"}}>
            {val.map((ele, idx) => (
              <div style={{padding:"2em"}}>
              <Card
                hoverable
                style={{ width: "18em" }}
                cover={
                  <Image
                    onError={(e)=>{e.target.onError = null; e.target.src=imgHolder}}
                    src={ele.images[0] === undefined
                              ? ""
                              : ele.images[0].location}
                  />
                }
              >
                <Meta title={"$" + ele.price} description={ele.title} />
              </Card>
              </div>
            ))}
          </Carousel>
          </Container>
          
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  //console.log(state.saleout);
  const props = {
    houses: state.saleout.houses,
    loading: state.saleout.loading,
    error: state.saleout.error
  };

  return props;
}

export default connect(mapStateToProps)(injectIntl(HouseFrame));

