import {
  FETCH_HOUSE_BEGIN,
  FETCH_HOUSE_FAILURE,
  FETCH_HOUSE_SUCCESS
} from "../const/types";


import actions from "../actions";

const initialState = {
  houses: [],
  loading: false,
  error: null
};

export default function houseReducer(state = initialState, action) {
  switch (action.type) {
    case FETCH_HOUSE_BEGIN:
      return {
        ...state,
        loading: true,
        error: null
      };
    case FETCH_HOUSE_SUCCESS:
      return {
        ...state,
        loading: false,
        houses: action.payload.houses
      };
    case FETCH_HOUSE_FAILURE:
      return {
        ...state,
        loading: false,
        error: action.payload.error,
        houses: action.houses
      };
    default:
      return state;
  }
}
