import React, { Component } from "react";
import { connect } from "react-redux";
import { injectIntl, FormattedMessage } from "react-intl";
import { Image, Row } from "react-bootstrap";
import settings from "../../../const/scrollsetting";
import TableShower from "../../Common/States/tableshower";
import "antd/dist/antd.css";
import { Carousel, Empty, Typography, Divider } from "antd";
import imgHolder from "../../../img/imagesHolder.png";
import LeaveMessage from "../../Common/States/leaveMessage"
import update from "immutability-helper";

import "./detailframe.scss";

const { Title, Paragraph, Text } = Typography;
class DetailInfoFrame extends Component {
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  handleEmptyImg(ele) {
    let dom;
    console.log(ele);
    dom = (
      <Image
        className="detail-img"
        src={ele.location === undefined ? "" : ele.location}
        onError={e => {
          e.target.onError = null;
          e.target.src = imgHolder;
        }}
        style={{ height: "16em" }}
      />
    );
    //console.log(dom)

    return dom;
  }

  render() {
    let img =
      this.props.info.images === undefined ? [""] : this.props.info.images;
    let house = this.props.info;
    if (Object.keys(house).length === 0 && house.constructor === Object) {
      house = { info: {} };
    }
    console.log(house);
    let description = house.info.description;
    console.log(description);
    return (
      <div>
        <Carousel autoplay {...settings}>
          {img.map((ele, idx) => (
            <div className="detail-img">{this.handleEmptyImg(ele)}</div>
          ))}
        </Carousel>

        <Row style={{marginTop:"4em"}}>
          <h3>{"$" + house.price}</h3>
          <hr/>
        </Row>
        <Row style={{marginTop:"2em"}}>
          <div>{this.handleStr("DETAIL_INFO")}</div>
        </Row>
        <Row style={{marginTop:"2em"}} id="detail-table">
          <TableShower table={house.info} />
        </Row>
        <Row>
        <hr/>
          <Typography>
            <Title level={3}>{this.handleStr("DESCRIPTION")}</Title>
             <hr/>
            <Paragraph className="text-left">{description}</Paragraph>
          </Typography>
        </Row>
        <Row style={{marginTop:"6em"}}>
            <LeaveMessage />
        </Row>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return state;
}

export default connect(mapStateToProps)(injectIntl(DetailInfoFrame));
