import React, { Component } from "react";
import { connect } from "react-redux";
import { injectIntl, FormattedMessage } from "react-intl";
import { Breadcrumb } from "react-bootstrap";
import { Link } from "react-router-dom";

class Service extends Component {
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  render() {
    //console.log(this.props.services)
    return (
      
      <div>
        <Breadcrumb className="services-breadcrumb">
          {this.props.services.map((ele, idx) => (
            <Breadcrumb.Item>
              <Link to={ele.link}>{this.handleStr(ele.value)}</Link>
            </Breadcrumb.Item>
          ))}
        </Breadcrumb>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return state;
}

export default connect(mapStateToProps)(injectIntl(Service));
