import React, { Component } from "react";
import Slider from "react-slick";
import { connect } from "react-redux";
import { injectIntl, FormattedMessage } from "react-intl";
import settings from "../../../const/scrollsetting";
import InfoTag from "../../Common/States/infotag";
import fetchNewListing from "../../../actions/newListingAction";
import fetchHouses from "../../../actions/houseAction";
import { Image, Jumbotron, Container } from "react-bootstrap";
import * as query from "../../../const/api";
import {
  Route,
  Link,
  BrowserRouter as Router,
  withRouter
} from "react-router-dom";
import { fetchHouseBegin } from "../../../actions/houseAction";

class Carousel extends Component {
  constructor() {
    super();
    this.state = {
      houses: [],
      loading: false,
      error: false
    };
  }
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  handleMore = () => {
    this.props.dispatch(fetchHouses(query.BASE_QUERY + "list/mylist"));
  };

  componentDidMount() {
    this.props.dispatch(fetchNewListing(query.FETCH_LATEST_LISTING));
  }
  //todo Link to search the same critiria and check the zonging for titel+for type
  render() {
    let val = this.props.houses === undefined ? [] : this.props.houses;
    return (
      <div id="main-carousel" style={{ overflow: "hidden"}}>
        <Slider {...settings}>
          {val.map((ele, idx) => (
            <div>
              <Jumbotron
                fluid
                mx-auto
                style={{
                  backgroundImage: `url(${ele.images[0].location}),url(${ele.images[0].location})`
                }}
              >
                <Container>
                  <InfoTag
                    zoning={"For " + ele.forType}
                    price={ele.price}
                    addr={ele.loc.addr}
                    type={ele.type}
                    id={ele.id}
                    style={{ position: "absoulte" }}
                  />
                </Container>
              </Jumbotron>
            </div>
          ))}
        </Slider>
      </div>
    );
  }
}

function mapStateToProps(state) {
  //console.log(state.newListing);
  const props = {
    houses: state.newListing.houses,
    loading: state.newListing.loading,
    error: state.newListing.error
  };

  return props;
}

export default connect(mapStateToProps)(injectIntl(Carousel));

