import {
    FETCH_DETAIL_BEGIN,
    FETCH_DETAIL_FAILURE,
    FETCH_DETAIL_SUCCESS,
} from "../const/types";
  
  
  import actions from "../actions";
  
  const initialState = {
    house: {},
    loading: false,
    error: null
  };
  
  export default function houseReducer(state = initialState, action) {
    switch (action.type) {
      case FETCH_DETAIL_BEGIN:
        return {
          ...state,
          loading: true,
          error: false
        };
      case FETCH_DETAIL_SUCCESS:
        return {
          ...state,
          loading: false,
          error:false,
          house: action.payload.houses
        };
      case FETCH_DETAIL_FAILURE:
        return {
          ...state,
          loading: false,
          error: true,
          house: action.houses
        };
      default:
        return state;
    }
  }
  