import React, { Component } from "react";
import "./NavBar.scss";
import Logo from "../Common/Stateless/logo.js";
import Address from "../Common/States/address.js";
import SocialMedia from "../Common/States/socialmedia.js";
import Login from "../Common/States/login.js";
import PagesNav from "../Common/Stateless/pagesnav";
import SearchPOP from "../NavBar/searchpop.js";
import { connect } from "react-redux";
import actions from "../../actions/index";
//import intl from 'react-intl-universal';
import { injectIntl, FormattedMessage } from "react-intl";
import { Button, Collapse,NavDropdown } from "react-bootstrap";
import SUPPORT_LOCALES from "../../const/supportlocales";

class Nav extends Component {
  constructor() {
    super();
    this.state = {
      isSearchShow: false
    };
  }

  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  handleSearchClick = () => {
    this.setState({
      isSearchShow: !this.state.isSearchShow
    });
  };

  render() {
    return (
      <div>
        <div className="nav_Line1 adressfont" >
          <Logo />
          <Address key="ADDRESS" />
          {/* <SocialMedia/>
                    <Login/>   */}
          {this.renderLocaleSelector()}
        </div>
        <div className="nav_Line2">
          <PagesNav />
          {this.renderSearchButton()}
        </div>
        {/*<div className="nav_Line3" style={{display:this.state.isSearchShow? 'block' : 'none'}}>
                 <Collapse in={this.state.isSearchShow}>
                    <SearchPOP id="search_pop" />
                  </Collapse>
                </div>*/}
        <div className="nav_Line3">
          <Collapse in={this.state.isSearchShow}>
            <div id="search_pop">
              <SearchPOP />
            </div>
          </Collapse>
        </div>
      </div>
    );
  }

  renderLocaleSelector() {
    return (
      <select
        className="mdb-select md-form colorful-select dropdown-primary"
        onChange={this.onSelectLocale}
        defaultValue=""
      >
        <option value="" disabled>
          Change Language
        </option>
        {SUPPORT_LOCALES.map(locale => (
          <option key={locale.value} value={locale.value}>
            {locale.name}
          </option>
        ))}
      </select>
    );
  }

  // renderSearchButton=()=>{
  //   return(
  //     <button  value="search"onClick={this.handleSearchClick} >{this.handleStr("SEARCH")}</button>
  //   )
  // }

  renderSearchButton = () => {
    return (
      <Button
        variant="primary"
        value="search"
        onClick={this.handleSearchClick}
        aria-controls="search_pop"
        aria-expanded={this.props.isSearchShow}
      >
        <i className="fas fa-search"></i>
      </Button>
    );
  };

  onSelectLocale = e => {
    let lang = e.target.value;
    console.log("chang lang " + lang);
    this.props.changeLanguage(lang);
  };
}

function mapStateToProps(state) {
  const props = {
    locale: state.navbar.language
  };
  return props;
}

const mapDispatchToProps = (dispatch, ownProps) => ({
  changeLanguage: val => dispatch(actions.changeLanguage(val))
});

export default connect(mapStateToProps, mapDispatchToProps)(injectIntl(Nav));
