import * as types from "../const/types";
import option from "../const/fetchoption"

export const fetchBrokageBegin = () => ({
  type: types.FETCH_BROKAGE_BEGIN
});

export const fetchBrokageSuccess = houses => ({
  type: types.FETCH_BROKAGE_SUCCESS,
  payload: { houses }
});

export const fetchBrokageFailure = error => ({
  type: types.FETCH_BROKAGE_FAILURE,
  payload: { error }
});

export default function fetchHouses(query) {
  return dispatch => {
    dispatch(fetchBrokageBegin());
    return fetch(query,option)
      .then(res => {
        //console.log(res)
        return res.json()
      })
      .then(json => {
        //console.log(json)
        dispatch(fetchBrokageSuccess(json));
        return json;
      })
      .catch((error) => {
        //console.log(error)
        dispatch(fetchBrokageFailure(error))});
  };
}
