import React, { Component } from "react";
import { connect } from "react-redux";
import { Nav, NavItem, ButtonToolbar, Button, Row, Col } from "react-bootstrap";
import { injectIntl, FormattedMessage } from "react-intl";
import areaCode from "../../../const/areacode";
import listType from "../../../const/listType";
import * as query from "../../../const/api";
import fetchHouses from "../../../actions/houseAction";

import './search.scss';

class Search extends Component {
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  componentDidMount(){

    let search = this.handleQuery(query.BASE_QUERY);
    this.props.dispatch(fetchHouses(search));
  }

  constructor() {
    super();
    this.state = {
      city: "Unlimited",
      listType: "Unlimited",
      min: 1,
      max: 40000000,
      parkSpace: "1-2",
      bedRoom: "1",
      room: "1"
    };
  }

  handleSearch = () => {
    let search = this.handleQuery(query.BASE_QUERY);
    this.props.dispatch(fetchHouses(search));
  };

  handleQuery = query => {
    query += "search.json?";
    if (this.state.city !== "Unlimited") {
      query += `rid=${this.state.city}&`;
    }
    if (this.state.listType !== "Unlimited") {
      query += `forType=${this.state.listType}&`;
    }
    query += `minprice=${this.state.min}&`;
    query += `maxprice=${this.state.max}&`;
    let rooms = this.handleNum(this.state.room);
    query += `maxroom=${rooms.max}&`;
    query += `minroom=${rooms.min}&`;

    query += "state=NORMAL";
    return query;
  };

  handleNum = num => {
    let res = {};
    switch (num) {
      case "1":
        res = { min: 1, max: 1 };
        break;
      case "1-2":
        res = { min: 1, max: 2 };
        break;
      case "2":
        res = { min: 2, max: 2 };
        break;
      case "3":
        res = { min: 3, max: 3 };
        break;
      case "3-4":
        res = { min: 3, max: 4 };
        break;
      case "4+":
        res = { min: 4, max: 1000 };
        break;
      case "5+":
        res = { min: 5, max: 1000 };
        break;
      default:
        res = { min: 1, max: 1000 };
    }
    return res;
  };

  handleCity = val => {
    console.log(val.target.getAttribute("data"));
    this.setState(
      {
        city: val.target.getAttribute("data")
      },
      function() {
        this.handleSearch();
      }
    );
  };

  handleListType = val => {
    console.log(val.target.innerHTML);
    this.setState(
      {
        listType: val.target.innerHTML
      },
      function() {
        this.handleSearch();
      }
    );
  };

  handleMin = val => {
    console.log(val.target.value);
    this.setState(
      {
        min: val.target.value
      },
      function() {
        this.handleSearch();
      }
    );
  };

  handleMax = val => {
    console.log(val.target.value);
    this.setState(
      {
        max: val.target.value
      },
      function() {
        this.handleSearch();
      }
    );
  };

  handleParkingSpace = val => {
    console.log(val.target.value);
    this.setState(
      {
        parkSpace: val.target.value
      },
      function() {
        this.handleSearch();
      }
    );
  };

  handleBedRoom = val => {
    console.log(val.target.value);
    this.setState(
      {
        bedRoom: val.target.value
      },
      function() {
        this.handleSearch();
      }
    );
  };

  handleRoom = val => {
    //console.log(val.target.value);
    this.setState(
      {
        room: val.target.value
      },
      function() {
        this.handleSearch();
      }
    );
  };

  render() {
    return (
      <div className="search">
        <div id="cd-search" className="cd-search">
          <div className="cd-search-content">
            <form id="search-form">
              <Row>
                <Col md={2}>
                  <div class="col-md-2">
                    <h4>{this.handleStr("CITY")}</h4>
                  </div>
                </Col>
                <Col md={10}>
                  <ButtonToolbar>
                    {areaCode.map((ele, idx) => (
                      <Button
                        variant="light"
                        className="listingButton"
                        data={ele["id"]}
                        onClick={e => this.handleCity(e)}
                      >
                        {this.handleStr(
                          ele["val"].replace(/\s+/g, "").toUpperCase()
                        )}
                      </Button>
                    ))}
                  </ButtonToolbar>
                </Col>
              </Row>
              <Row>
                <Col md={2}>
                  <h4>{this.handleStr("LISTING_TYPE")}</h4>
                </Col>
                <Col md={10}>
                  <ButtonToolbar>
                    {listType.map((ele, idx) => (
                      <Button
                        variant="light"
                        className="listingButton"
                        data={ele["id"]}
                        onClick={e => this.handleListType(e)}
                      >
                        {this.handleStr(
                          ele["val"].replace(/\s+/g, "").toUpperCase()
                        )}
                      </Button>
                    ))}
                  </ButtonToolbar>
                </Col>
              </Row>
              <Row>
                <Col className="form-group col-md-5">
                  <label for="iornputAddress">{this.handleStr("FROM")}</label>
                  <input
                    type="text"
                    className="form-control"
                    id="inputAddress"
                    placeholder="$1"
                    onChange={e => this.handleMin(e)}
                  />
                </Col>
                <Col className="form-group col-md-5">
                  <label for="inputAddress2">{this.handleStr("TO")}</label>
                  <input
                    type="text"
                    className="form-control"
                    id="inputAddress2"
                    placeholder="$4000000"
                    onChange={e => this.handleMax(e)}
                  />
                </Col>
              </Row>
              <Row>
                <Col className="form-group col-md-4">
                  <label for="inputState">
                    {this.handleStr("PARKING_SPACE")}
                  </label>
                  <select
                    id="inputState"
                    className="form-control"
                    onChange={e => this.handleParkingSpace(e)}
                  >
                    <option selected>1-2</option>
                    <option>3-4</option>
                    <option>5+</option>
                  </select>
                </Col>
                <Col className="form-group col-md-4">
                  <label for="inputState">{this.handleStr("BEDROOM")}</label>
                  <select
                    id="inputState"
                    className="form-control"
                    onChange={e => this.handleBedRoom(e)}
                  >
                    <option selected>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4+</option>
                  </select>
                </Col>
                <Col className="form-group col-md-4">
                  <label for="inputState">{this.handleStr("ROOM")}</label>
                  <select
                    id="inputState"
                    className="form-control"
                    onChange={e => this.handleRoom(e)}
                  >
                    <option selected>1</option>
                    <option>2</option>
                    <option>3</option>
                    <option>4+</option>
                  </select>
                </Col>
              </Row>
            </form>
          </div>
        </div>
      </div>
    );
  }
}

function mapStateToProps(state) {
  //console.log(state);
  return state;
}

export default connect(mapStateToProps)(injectIntl(Search));
