import * as types from "../const/types";
import option from "../const/fetchoption"

export const fetchNewListingBegin = () => ({
  type: types.FETCH_MAIN_CAROUSEL_BEGIN
});

export const fetchNewListingSuccess = houses => ({
  type: types.FETCH_MAIN_CAROUSEL_SUCCESS,
  payload: { houses }
});

export const fetchNewListingFailure = error => ({
  type: types.FETCH_MAIN_CAROUSEL_FAILURE,
  payload: { error }
});

export default function fetchNewListing(query) {
  return dispatch => {
    dispatch(fetchNewListingBegin());
    return fetch(query,option)
      .then(res => {
        //console.log(res)
        return res.json()
      })
      .then(json => {
        //console.log(json)
        dispatch(fetchNewListingSuccess(json));
        return json;
      })
      .catch((error) => {
        //console.log(error)
        dispatch(fetchNewListingFailure(error))});
  };
}
