import React, { Component } from "react";
import Slider from "react-slick";
import settings from "../../../const/scrollsetting";
import img from "../../../img/images-solid.svg";
import { connect } from "react-redux";
import { injectIntl, FormattedMessage } from "react-intl";
import imgHolder from "../../../img/imagesHolder.png";
import "antd/dist/antd.css";
import {
  Nav,
  Image,
  NavItem,
  Button,
  ButtonToolbar,
  Container
} from "react-bootstrap";
import InfoTag from "../../Common/States/infotag";
import HouseFrame from "./houseframe";
import areaCode from "../../../const/areacode";
import fetchNewBrokage from "../../../actions/brokageAction";
import * as query from "../../../const/api";
import { Card, Tag, Tabs, Carousel } from "antd";
import update from "immutability-helper";

const { TabPane } = Tabs;
class ListingShowCase extends Component {
  //ret a string of locale message
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  changeTab = activeKey => {
    console.log(activeKey);
    let type;
    switch (activeKey) {
      case "1":
        type = "SALE";
        break;
      case "2":
        type = "LEASE";
        break;
      default:
        type = "SALE";
        break;
    }
    this.setState({
      activeKey: activeKey,
      forType: type
    });
  };

  constructor() {
    super();
    this.state = {
      area: "11",
      loading: false,
      error: false,
      select: 0,
      forType: "SALE",
      activeKey: "1"
    };
  }

  handleClick = val => {
    // use fallback to wait for state mutate
    this.setState({
      area:
        val.currentTarget.getAttribute("data") === null
          ? 1
          : val.currentTarget.getAttribute("data")
    });
  };

  handleSelect = e => {
    console.log(e.target.getAttribute("data"));
    this.setState({
      select: e.target.getAttribute("data")
    });
  };

  handleShowCase = ele => {
    console.log(this.state.area);
    let houses = ele === undefined ? [] : ele;
    console.log(parseInt(this.state.area, 10));
    let res = [];

    for (let house in houses) {
      if (house !== undefined && house !== null) {
        // console.log("region id: "+parseInt(this.state.area,10) == houses[house].loc.regionId)
        // console.log("fortype : "+houses[house].forType == this.state.forType)
        // console.log(this.state.forType)
        // console.log(this.state.area)
        if (
          parseInt(this.state.area, 10) === houses[house].loc.regionId &&
          houses[house].forType === this.state.forType
        ) {
          res.push(houses[house]);
        }
      }
    }
    console.log(res);
    return res;
  };

  componentDidMount() {
    this.props.dispatch(fetchNewBrokage(query.FETCH_LATEST_BROKAGE));
  }

  //todo: house frame should be in same component
  render() {
    let house = this.handleShowCase(this.props.houses);
    let settingsCopy = update(settings, {
      dots: { $set: false },
      slidesToShow: { $set: 4 },
      slidesToScroll: { $set: 4 },
      responsive: { $set: [] }
    });
    console.log(house);
    return (
      <div id="list-showcase">
        <h3>{this.handleStr("PROMOTE_LISTING")}</h3>
        <Container>
          <ButtonToolbar>
            {areaCode.map((ele, idx) => (
              <Button
                variant="light"
                className="listingButton"
                data={ele["id"]}
                onClick={e => this.handleClick(e)}
              >
                <a>
                  {this.handleStr(ele["val"].replace(/\s+/g, "").toUpperCase())}
                  <p>{ele["val"]}</p>
                </a>
              </Button>
            ))}
          </ButtonToolbar>
          <div
            className="card-container"
            style={{ paddingTop: 20, paddingBottom: 20 }}
          >
            <Tabs
              type="card"
              activeKey={this.state.activeKey}
              onChange={this.changeTab}
            >
              <TabPane tab={this.handleStr("SALE")} key="1">
                <HouseFrame ele={house[this.state.select]} />
              </TabPane>
              <TabPane tab={this.handleStr("LEASE")} key="2">
                <HouseFrame ele={house[this.state.select]} />
              </TabPane>
            </Tabs>
          </div>
          <Carousel {...settingsCopy} style={{marginBottom:"6em"}}>
            {house.map((ele, idx) => (
              <div>
                <Card
                  hoverable
                  style={{ width: "15em", padding: "0.5em" }}
                  data={idx}
                  onClick={e => this.handleSelect(e)}
                  cover={
                    <Image
                      src={
                        ele.images[0] === undefined
                          ? ""
                          : ele.images[0].location
                      }
                      onError={e => {
                        e.target.onError = null;
                        e.target.src = imgHolder;
                      }}
                    />
                  }
                >
                  <Card.Meta title={"$" + ele.price} description={ele.title} />
                </Card>
              </div>
            ))}
          </Carousel>
        </Container>
      </div>
    );
  }
}

// function mapStateToProps(state) {
//   return state;
// }

function mapStateToProps(state) {
  const props = {
    houses: state.newBrokage.houses,
    loading: state.newBrokage.loading,
    error: state.newBrokage.error
  };

  return props;
}

export default connect(mapStateToProps)(injectIntl(ListingShowCase));

