import React, { Component } from "react";
import { connect } from "react-redux";
import "../../NavBar/NavBar.scss";
import { injectIntl } from "react-intl";

import { Card, Button, Accordion } from "react-bootstrap";


class Accord extends Component {
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };

  render() {
    return (
      <div>
        <Card style={{ width: "16rem",marginTop:"2rem" }}>
          <Card.Body>
            <Card.Title>{this.props.title}</Card.Title>
            <Accordion >
            {this.props.accordions.map((ele, idx) => (
              <Card>
                <Accordion.Toggle as={Card.Header} eventKey={idx} style={{fontSize:"0.8em"}} >
                <i className="fas fa-plus" style={{marginRight:"0.4em",float:"left"}}></i>{ele.Title}
                </Accordion.Toggle>
                <Accordion.Collapse eventKey={idx}>
                  <Card.Body style={{ backgroundColor:"rgb(253, 253, 253)",fontSize:"0.8em"}}>{ele.Body}</Card.Body>
                </Accordion.Collapse>
              </Card>
            ))}
            </Accordion>
          </Card.Body>
        </Card>
      </div>
    );
  }
}

function mapStateToProps(state) {
  return state;
}

export default connect(mapStateToProps)(injectIntl(Accord));
