import React, { Component } from "react";
import Slider from "react-slick";
import { connect } from "react-redux";
import { injectIntl, FormattedMessage } from "react-intl";
import { Row, Col } from "react-bootstrap";
import bed from "../../../img/bed.png";
import parking from "../../../img/parking.png";
import chair from "../../../img/chair.png";
import settings from "../../../const/scrollsetting";
import { Table, Divider, Tag } from 'antd';

const columns = [
  {
    title: 'Name',
    dataIndex: 'name1',
    key: 'name1',
    render: text => <b>{text}</b>,
  },
  {
    title: 'Property',
    dataIndex: 'property1',
    key: 'proplerty1',
  },
  {
    title: 'Name',
    dataIndex: 'name2',
    key: 'name2',
    render: text => <b>{text}</b>,
  },
  {
    title: 'Property',
    dataIndex: 'property2',
    key: 'proplerty2',
  },

]


class TableShower extends Component {
  //ret a string of locale message
  
  constructor() {
    super();
    this.state = {
      data:{}
    };
  }

  
  handleStr = val => {
    const { intl } = this.props;
    return intl.formatMessage({ id: val });
  };


 handleTable=()=>{
    let i = 1;
    let j = 0;
    let data = []
    let table = this.props.table
    for(let key in table){
       if(key !==null && key !=="description"){
          if(i%2 === 1){
            data.push({key:i,name1:this.handleStr(key.replace(/\s+/g,"").toUpperCase()),property1:table[key]})
          }else{
            let tmp = data[j]
           
            data[j] = Object.assign({},tmp,{name2:this.handleStr(key.replace(/\s+/g,"").toUpperCase()),property2:table[key]})
            j++
          }
          i++
         
       }
    }
  
    return data
 }
  


  render() {
    // console.log(this.props.table)
    return (
      <div>
          <Table columns={columns} dataSource={this.handleTable()} />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return state;
}

export default connect(mapStateToProps)(injectIntl(TableShower));

