import React, { Component } from 'react';
import { addLocaleData, IntlProvider } from 'react-intl';
import { connect } from 'react-redux';
import zh_CN from './zh_CN';
import en_CA from './en_CA';
import zh_HK from './zh_HK'
// import zh from 'react-intl/locale-data/zh';
// import en from 'react-intl/locale-data/en';

// addLocaleData([...zh,...en]);

class Inter extends Component {
  render() {
    let { locale, localeMessage, children } = this.props;
    return (
      <IntlProvider key={locale} locale={locale} messages={localeMessage}>
        {children}
      </IntlProvider>
    )
  }
};



function chooseLocale(val) {
  //console.log("val waiting to switch "+val)
  let _val = val || navigator.language.split('_')[0];
  if(navigator.language === 'zh-HK' || navigator.language ==='zh-TW'|| val==='HK') _val = 'hk';
  
  //console.log("begin switch lang "+ _val)
  switch (_val) {
    case 'en':
      return en_CA;
    case 'zh':
      return zh_CN;
    case 'hk':
      return zh_HK;
    default:
      return zh_CN;
  }
}

function mapStateToProps(state,ownProps){
   
  // console.log(state)
  const props = {
      local: state.navbar.language,
      localeMessage: chooseLocale(state.navbar.language)
   }
  
   return props;
}

let Intl = connect(mapStateToProps)(Inter);

export default Intl;












  